h2 {
    text-align: center;
}
.NewsBlock {
    width: 600px;
    height: 570px;
    margin-left: 30px;
}
.NewsImg {
    width: 220px;
    height: 130px;
}
.NewsTitle {
    width: 350px;
    height: 130px;
    display: block;
    float: right;

}

.newsPages {
   display: block;
    cursor: pointer;
    height: 25px;
    width: 650px;
}
.newsPages a{
    margin-left: 30px;
    padding-left: 5px;
}

.newsPages a:hover{
    font-weight: bold;
}

.newsPages a:active{
    font-weight: bold;
}

.selectedPage {
    font-weight: bold;
    font-size: 21px;
}