.User img{
    margin-top: 15px;
    widht: 50px;
    height: 80px;
    border-radius: 50px;
}
.User {
    width: 500px;
    display:  table-cell;
    height: 100px;
    border-bottom:solid black 1px;
}
.User :hover {
    offset: 50%;
}
.Name {
    word-wrap: break-word;
    position: absolute;
    margin-top: 10px;
    margin-left: 20px;
    width: 350px;
    font-size: 20px;
}
.User button {
    position: absolute;
    margin-top: 65px;
    margin-left: 20px;
}
.Status {
    position: absolute;
    margin-top: 40px;
    margin-left: 20px;
    width: 350px;
    font-size: 18px;
}

.selectedPage {
    font-weight: bold;
    margin-right: 10px;
}

.loading {
    width: 50px;
    background-color: #F0EBEB;
}

.activeUser {
    font-weight: bold;
    margin-right: 10px;
}


